.container.home {
	width: 100%;
	margin: 0px auto;
	min-height: 75vh;
	background: linear-gradient(
			to right,
			#030303b4,
			#00000019 60%,
			#08080816 40%,
			#ffffff00
		),
		linear-gradient(to top, #030303b4, #00000019 60%, #08080816 40%, #ffffff00);
	display: flex;
	justify-content: center;
	flex-direction: column;
	height: 100%;
	padding: 25px;
	flex-wrap: wrap;
}
.pz__10 {
