}
.left {
	text-align: left !important;
}
.row {
	display: flex;
	width: 90%;
	margin: 20px auto;
	flex-wrap: wrap;
	overflow: hidden;
	align-items: stretch;
	justify-content: center;
	min-height: 100%;
	grid-gap: 30px;
	gap: 30px;
}
.row-service {
	display: flex;
	width: 97%;
	margin: 20px auto;
	flex-wrap: wrap;
	overflow: hidden;
	align-items: stretch;
	justify-content: center;
	min-height: 100%;
	grid-gap: 30px;
	gap: 30px;
}
.grid__2 {
	display: grid;
	width: 90%;
	grid-template-columns: 1fr 1fr;
	grid-gap: 30px;
	gap: 30px;
	margin: 20px auto;
}
.grid-col__2 {
	display: flex;
	width: 100%;
	align-items: center;
	justify-content: center;
	height: 100%;
}
.col__2 {
	flex: 1 1;
	display: flex;
	justify-content: center;
	flex-direction: column;
	align-items: flex-start;
	position: relative;
	overflow: hidden;
}
.col__3 {
	max-width: 33.333333%;
	display: flex;
	padding: 20px;
	justify-content: center;
	flex-direction: column;
	margin-top: 20px;
}
.component__space {
	padding: 30px 0px;
}
.pointer {
	cursor: pointer;
}
svg {
	stroke-width: 1 !important;
}
.relative {
	position: relative;
}
.absolute {
	position: absolute;
}
