	color: rgba(198, 201, 216, 0.75);
}
button.btn {
	font-size: 18px;
	font-weight: 600;
	border-radius: 5px;
	border: 2px solid #368777;
	background-color: #368777;
	color: #000 !important;
	text-transform: uppercase;
	/* letter-spacing: 2px; */
	transition: 0.4s all ease;
	height: 50px;
	width: 180px;
	margin: 0px 0px;
}
.p__color {
	color: rgba(0, 0, 0, 0.75);
	font-size: 18px;
	line-height: 30px;
	font-family: Poppins, sans-serif;
	margin-bottom: 15px;
}
button.about.btn.pointer:hover {
	background: #f9004fb9;
	color: #fff !important;
}

.button__primary {
	padding: 11px 35px;
	display: flex;
	align-items: center;
	justify-content: center;
	border-radius: 6px;
	border: none;
	background-color: #368777;
	color: #fff;
	font-size: 19px;
	font-weight: 600;
	text-shadow: 0px 0px 1px #000000db;
	max-width: 350px;
	width: 290px !important;
	margin-top: 30px;
	cursor: pointer;
}

/* RESPONSIVE */
@media only screen and (max-width: 768px) {
	.col__2 {
		max-width: 100%;
	}
	.grid__2 {
		width: 90%;
		grid-template-columns: 1fr;
		grid-gap: 30px;
		gap: 30px;
		margin: 20px auto;
	}
}
@media only screen and (max-width: 768px) {
	.row.reverse {
		flex-direction: column-reverse;
	}
	.row {
		flex-direction: column;
	}
}
@media only screen and (max-width: 1120px) {
	.row-service {
		flex-direction: column;
		width: 100%;
		grid-gap: 15px;
		gap: 15px;
		padding: 0 15px;
	}
}
@media only screen and (max-width: 768px) {
	.col__3 {
		max-width: 50%;
	}
}
@media only screen and (max-width: 425px) {
	.col__3 {
		max-width: 100%;
	}
}
@media only screen and (max-width: 425px) {
